'use client';
import React, { useState } from 'react';
import { API_BASE, DEV_TOKEN } from '../../../../lib/env';

export default function PrivacyPage({ params }: { params: Promise<{ id: string }> }) {
  const [to, setTo] = useState('0x0000000000000000000000000000000000000000');
  const [pg, setPg] = useState('pg1');
  const [hash, setHash] = useState<string | null>(null);
  const [busy, setBusy] = useState(false);

  async function sendTx(e: React.FormEvent) {
    e.preventDefault();
    setBusy(true);
    try {
      const org = await params;
      const res = await fetch(`${API_BASE}/api/besu/firefly/${org.id}/tx/private`, {
        method: 'POST',
        headers: { 'content-type': 'application/json', authorization: `Bearer ${DEV_TOKEN}` },
        body: JSON.stringify({ to, privacyGroupId: pg, data: '0x' }),
      }).then((r) => r.json());
      setHash(res.besu_tx_hash || JSON.stringify(res));
    } finally {
      setBusy(false);
    }
  }

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">Privacy groups</h3>
      <form onSubmit={sendTx} className="grid grid-cols-1 md:grid-cols-3 gap-3 border border-gray-800 rounded p-3 bg-gray-900">
        <input className="border border-gray-700 rounded p-2 bg-gray-900" value={to} onChange={(e) => setTo(e.target.value)} placeholder="to address" />
        <input className="border border-gray-700 rounded p-2 bg-gray-900" value={pg} onChange={(e) => setPg(e.target.value)} placeholder="privacyGroupId" />
        <button disabled={busy} className="px-3 py-2 rounded bg-indigo-600 text-white disabled:opacity-50">
          {busy ? 'Sending...' : 'Send private tx'}
        </button>
      </form>
      {hash && (
        <div className="text-sm border border-gray-800 rounded p-3 bg-gray-900">
          besu_tx_hash: <code className="px-1 py-0.5 bg-gray-200 rounded">{hash}</code>
        </div>
      )}
    </div>
  );
}

